@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.img-scale{
  overflow: hidden;
  img{
    transition: all .3s;
  }
  &:hover{
    img{
      transform: scale(1.1);
    }
  }
}

.tabs{
  display: flex;
  align-items: center;
}

.tab{
  display: flex;
  align-items: center;
  font-size: 18px;
  line-height: 22px;
  color: #BDBDBD;
  transition: all .3s;
  white-space: nowrap;
  flex-shrink: 0;
  padding-bottom: 14px;
  border-bottom: 1px solid transparent;
  &:hover{
    color: $black;
    cursor: pointer;
  }
  &._active{
    border-color: $black;
    color: $black;
    pointer-events: none;
  }
  &:not(:last-child){
    margin-right: 55px;
  }
  &-content{
    display: none;
    &._active{
      display: block;
    }
  }
}

.map{
  height: 100%;
  width: 100%;
  .ymaps-2-1-79-map-copyrights-promo,
  .ymaps-2-1-79-copyright__content{
    opacity: 0;
    pointer-events: none;
  }
}

.dish-grid{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 80px / 3));
  gap: 110px 40px;
}

.dish-grid2{
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 45px));
  gap: 93px 53px;
  .dish-card2{
    &__info{
      display: block;
      margin-top: 34px;
    }
    &__title{
      font-size: 16px;
    }
    &__price{
      margin: 23px 0;
      width: 100%;
    }
    &__weight{
      margin-top: 17px;
    }
  }
}


@media screen and (max-width: 1100px){
  .dish-grid2{
    grid-template-columns: repeat(3,calc(100% / 3 - 80px / 3));
    gap: 93px 40px;
  }
}


@media screen and (max-width: 992px){
  .dish-grid{
    grid-template-columns: repeat(2,calc(50% - 20px));
  }
}


@media screen and (max-width: 768px){
  .dish-grid2{
    display: flex;
    gap: unset;
    width: calc(100% + 32px);
    margin-left: -16px;
    padding: 0 16px 30px;
    overflow-x: auto;
    .dish-card2{
      flex-shrink: 0;
      width: 244px;
      &:not(:last-child){
        margin-right: 34px;
      }
      &__price{
        position: static;
        margin: 8px 0 50px;
      }
      &__weight{
        margin-bottom: 0;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .dish-grid{
    grid-template-columns: 100%;
    gap: 87px;
  }

  .tab{
    font-size: 16px;
    line-height: 125%;
    padding-bottom: 8px;
    &:not(:last-child){
      margin-right: 32px;
    }
  }
}
