.modal-backdrop {
    &.show {
        opacity: 1;
        background: $gray;
    }
}

.modal{
    text-align: center;
    &:before {
        @media screen and (min-width: 768px){
            display: inline-block;
            vertical-align: middle;
            content: " ";
            height: 100%;
        }
    }
    &-dialog{
        display: inline-block;
        text-align: left;
        vertical-align: middle;
        pointer-events: unset;
        width: 100%;
        max-width: calc(100% - 34px);
    }
    &__close{
        position: absolute;
        top: 48px;
        right: 40px;
    }
}

.restaurant-modal{
    .modal{
        &-dialog{
            width: 1400px;
            padding: 73px 119px;
            background: #FFFFFF;
        }
    }
    &__grid{
        display: grid;
        grid-template-columns: repeat(4, calc(25% - 30px));
        gap: 40px;
        margin-top: 53px;
    }
}

.address-modal{
    .modal{
        &-dialog{
            width: 970px;
            padding: 73px 64px 53px;
            background: #FFFFFF;
        }
    }
    &__title{
        text-transform: uppercase;
    }
    &__tabs{
        margin: 37px 0 50px;
    }
    &__delivery{
        display: grid;
        grid-template-columns: auto 100px 100px;
        gap: 40px;
    }
    .fg{
        input,
        select{
            font-size: 14px;
            height: 45px;
        }
    }
    &__map{
        height: 412px;
        margin-top: 56px;
    }
    &__nav{
        display: flex;
        justify-content: center;
        margin-top: 40px;
    }
    &__hint{
        max-width: 394px;
        padding: 18px 20px;
        background-color: $yellowBg;
        font-size: 14px;
        line-height: 17px;
        margin: -14px 0 36px;
    }
}

.reg-modal{
    .modal{
        &-dialog{
            width: 430px;
            padding: 51px 51px 56px;
            background: $yellowBg;
        }
        &__close{
            top: 40px;
            right: 27px;
            img{
                width: 14px;
            }
        }
    }
    &__title{
        text-transform: uppercase;
        text-align: center;
    }
    &__desc{
        margin: 28px auto 0;
        max-width: 400px;
        text-align: center;
        font-size: 16px;
        line-height: 139.5%;
    }
    &__form{
        margin: 24px auto 0;
        max-width: 200px;
        display: flex;
        flex-direction: column;
        align-items: center;
        .fg{
            width: 100%;
            input{
                text-align: center;
                font-size: 24px;
                height: 54px;
            }
            &--num{
                width: 60px;
            }
        }
    }
    &__btn{
        margin-top: 37px;
        padding: 13px 0;
        &:before{
            height: 2px;
        }
    }
    &__change-phone,
    &__hint{
        font-size: 14px;
        line-height: 17px;
        color: $gray;
    }
    &__hint{
        margin: 28px auto 0;
        max-width: 335px;
        text-align: center;
        a{
            text-decoration: underline;
        }
    }
    &__change-phone{
        margin: 11px auto 0;
        display: flex;
        &:hover{
            color: $black;
        }
    }
    &__btn2{
        font-weight: 500;
        font-size: 16px;
        letter-spacing: 0.09em;
        text-transform: uppercase;
        margin: 46px 0 0;
        &:hover{
            color: $primary;
        }
    }
}

.reg-modal2{
    .modal {
        &-dialog {
            width: 592px;
        }
    }
    .reg-modal__form{
        max-width: unset;
    }
}

.order-modal{
    .modal {
        &-dialog {
            width: 430px;
            padding: 40px 28px;
            background: $yellowBg;
            display: inline-flex;
            flex-direction: column;
            align-items: center;
        }
        &__close{
            top: 23px;
            right: 17px;
        }
    }
    &__title{
        font-weight: 500;
        width: 100%;
    }
    &__items{
        margin-top: 5px;
        width: 100%;
    }
    &__item{
        padding: 23px 0;
        &:not(:last-child){
            border-bottom: $border;
        }
        &-title{
            color: $black;
        }
        &-info,
        &-title{
            font-size: 14px;
            line-height: 17px;
        }
        &-info{
            margin-top: 14px;
            display: flex;
            align-items: center;
            color: $gray;
        }
        &-count{
            margin-left: 19px;
        }
    }
    &__btn{
        padding: 10px 0;
        font-size: 14px;
        line-height: 100%;
        letter-spacing: 0.09em;
        text-transform: uppercase;
        &:before{
            height: 2px;
        }
    }
}

.privacy-modal{
    .modal {
        &-dialog {
            width: 1000px;
            padding: 72px 78px;
            background: #fff;
        }
        &__close{
            top: 48px;
            right: 31px;
        }
    }
    &__content{
        font-size: 16px;
        line-height: 125%;
        margin-top: 60px;
        p{
            line-height: inherit;
        }
    }
    &__btn{
        width: 217px;
        height: 45px;
        margin: 37px auto 0;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
    }
}

.order-modal2{
    .modal {
        &-dialog {
            width: 592px;
            padding: 47px 65px;
            background: $yellowBg;
            display: inline-flex;
            flex-direction: column;
            align-items: center;
        }
        &__close{
            top: 40px;
            right: 27px;
        }
    }
    &__title{
        text-align: center;
        text-transform: uppercase;
    }
    &__desc{
        margin: 39px auto 0;
        max-width: 354px;
        text-align: center;
        font-size: 16px;
        line-height: 139.5%;
    }
    &__btn{
        margin-top: 22px;
        padding: 15px 0;
        text-transform: uppercase;
        font-size: 16px;
        line-height: 100%;
        &:before{
            height: 2px;
        }
    }
    &__cancel{
        margin-top: 16px;
        font-size: 16px;
    }
}

.photo-modal{
    .modal {
        &-dialog {
            width: 628px;
            padding: 50px 34px 67px;
            background-color: #fff;
        }
        &__close{
            top: 56px;
            right: 34px;
        }
    }
    &__title{
        text-transform: uppercase;
    }
    &__grid{
        display: grid;
        grid-template-columns: repeat(4,1fr);
        gap: 30px;
        margin-top: 45px;
    }
}

.rating-modal{
    .modal {
        &-dialog {
            width: 280px;
            padding: 43px 47px 37px;
            background-color: $yellowBg;
            display: inline-flex;
            flex-direction: column;
            align-items: center;
        }
        &__close{
            top: 21px;
            right: 23px;
        }
    }
    &__title{
        text-transform: uppercase;
        width: 100%;
        font-weight: 500;
    }
    &__desc,
    &__desc2{
        width: 100%;
        font-size: 14px;
        line-height: 17px;
        margin-top: 28px;
    }
    &__desc2{
        color: $gray;
    }
    &__btn{
        margin-top: 28px;
        padding: 10px 0;
        font-size: 14px;
        line-height: 100%;
        letter-spacing: 0.09em;
        text-transform: uppercase;
        &:before{
            height: 2px;
        }
    }
}

.rating-modal2{
    .modal {
        &-dialog {
            width: 552px;
            padding: 66px 47px 58px;
            background-color: #fff;
        }
        &__close{
            top: 21px;
            right: 23px;
        }
    }
    &__title{
        text-transform: uppercase;
    }
    &__item{
        margin-top: 46px;
        display: grid;
        grid-template-columns: 140px auto;
        gap: 29px;
        align-items: center;
    }
    &__img{
        img{
            width: 100%;
            display: block;
        }
    }
    &__name{
        font-size: 16px;
        line-height: 125%;
    }
    &__input{
        display: flex;
        margin-top: 36px;
    }
    &__message{
        margin-top: 54px;
        textarea{
            height: 100px;
        }
        label{
            margin-bottom: 7px;
        }
    }
    &__nav{
        display: flex;
        align-items: center;
        justify-content: flex-end;
        margin-top: 42px;
    }
    &__step{
        font-size: 14px;
        line-height: 17px;
        color: $gray;
    }
    &__btn{
        margin-left: 39px;
        padding: 10px 0;
        font-size: 14px;
        line-height: 100%;
        letter-spacing: 0.09em;
        text-transform: uppercase;
        &:before{
            height: 2px;
        }
    }
}

.address-modal2{
    .modal {
        &-dialog {
            width: 628px;
            padding: 50px 34px;
            background-color: #fff;
        }
        &__close{
            top: 58px;
            right: 34px;
        }
    }
    &__title{
        text-transform: uppercase;
    }
    &__grid{
        margin-top: 40px;
        display: grid;
        grid-template-columns: repeat(6,1fr);
        gap: 40px;
    }
    &__full{
        grid-column: 1/-1;
    }
    .fg{
        input{
            height: 43px;
        }
    }
    &__nav{
        margin-top: 32px;
        display: flex;
        justify-content: center;
    }
    &__btn{
        font-size: 14px;
        line-height: 100%;
        padding: 10px 0;
    }
    &__map{
        margin-top: 60px;
        height: 272px;
    }
}


@media screen and (max-width: 1400px){
    .restaurant-modal{
        .modal{
            &-dialog{
                padding: 46px 50px;
            }
        }
    }
}


@media screen and (max-width: 1100px){
    .restaurant-modal{
        &__grid{
            grid-template-columns: repeat(3, calc(100% / 3 - 20px));
            gap: 80px 30px;
        }
    }
}


@media screen and (max-width: 992px){
    .restaurant-modal{
        &__grid{
            grid-template-columns: repeat(2, calc(50% - 15px));
        }
    }

    .reg-modal{
        padding: 80px 0 110px;
    }
}


@media screen and (max-width: 768px){
    .restaurant-modal{
        .modal{
            &__close{
                top: 31px;
                right: 25px;
                img{
                    width: 14px;
                }
            }
        }
        &__grid{
            grid-template-columns: 100%;
        }
        &__title{
            font-size: 20px;
            font-weight: 500;
            text-align: center;
        }
    }

    .address-modal{
        .modal{
            &-dialog{
                padding: 80px 30px 50px;
            }
            &__close{
                top: 26px;
                right: 30px;
                img{
                    width: 15px;
                }
            }
        }
        &__delivery{
            grid-template-columns: 1fr 1fr;
            .fg{
                &:nth-child(1){
                    grid-column: 1/-1;
                }
            }
        }
        &__map{
            margin-top: 40px;
            height: 250px;
        }
        &__link{
            font-size: 12px;
            line-height: 100%;
            &:before{
                height: 2px;
            }
        }
        &__hint{
            padding: 12px 25px;
            font-size: 12px;
            line-height: 15px;
        }
    }

    .privacy-modal{
        .modal{
            &-dialog{
                padding: 98px 17px;
                max-width: 100%;
                width: 100%;
                margin: 0;
            }
            &__close{
                top: 55px;
                right: 17px;
            }
        }
        &__title{
            font-size: 24px;
            line-height: 30px;
        }
        &__content{
            margin-top: 45px;
            font-size: 14px;
            line-height: 17px;
        }
    }

    .order-modal2{
        .modal-dialog{
            padding-left: 20px;
            padding-right: 20px;
        }
        .modal__close{
            top: 20px;
            right: 20px;
        }
    }

    .rating-modal2{
        padding: 63px 0 46px;
        &__title{
            font-size: 24px;
            line-height: 30px;
        }
        &__step{
            margin-top: 45px;
        }
        &__item{
            margin-top: 30px;
            grid-template-columns: 159px auto;
            gap: 27px;
        }
        &__input{
            margin-top: 30px;
        }
        &__message{
            margin-top: 80px;
        }
        &__nav{
            justify-content: center;
        }
        &__btn{
            margin: 0;
        }
    }

    .address-modal2{
        .modal{
            &-dialog{
                margin: 0;
                max-width: 100%;
                padding: 62px 16px 48px;
            }
            &__close{
                top: 40px;
                right: 17px;
            }
        }
        &__title{
            font-size: 16px;
            line-height: 20px;
        }
        &__grid{
            margin-top: 46px;
            grid-template-columns: repeat(4,1fr);
        }
        &__map{
            margin-top: 80px;
        }
        &__nav{
            margin-top: 49px;
        }
    }
}


@media screen and (max-width: 576px){
    .photo-modal{
        .modal{
            &-dialog{
                margin: 0;
                max-width: 100%;
                padding: 108px 16px;
            }
            &__close{
                top: 36px;
                right: 17px;
            }
        }
        &__title{
            font-size: 24px;
            line-height: 30px;
            text-align: center;
        }
        &__grid{
            margin: 63px auto 0;
            width: 118px;
            grid-template-columns: 100%;
            gap: 38px;
        }
    }
}


@media screen and (max-width: 380px){
    .restaurant-modal{
        .modal-dialog{
            padding-left: 20px;
            padding-right: 20px;
        }
    }
}
