.main{
  //background-color: $yellowBg;
  padding: 203px 0 84px;
  margin-top: -$headerHeight;
  position: relative;
  &__slider{
    position: relative;
  }
  &__slide{
    display: grid;
    grid-template-columns: 371px calc(100% - 400px);
    gap: 29px;
    align-items: center;
  }
  &__subtitle{
    margin-bottom: 24px;
  }
  &__title{
    font-weight: 400;
  }
  &__order{
    margin-top: 75px;
    padding: 16px 0;
    font-size: 16px;
    letter-spacing: 0.09em;
    text-transform: uppercase;
    &:before{
      background: #D9D9D9;
    }
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-button{
      &-prev{
        left: -70px;
      }
      &-next{
        right: -70px;
      }
    }
  }
}

.dish-section{
  &__nav{
    display: flex;
    justify-content: flex-end;
  }
  &__link{
    text-transform: uppercase;
  }
  &__grid{
    margin-top: 91px;
  }
}

.new,
.pizza,
.sushi,
.salad{
  &__title{
    font-weight: 400;
  }
}

.new{
  padding: 85px 0 125px;
  &__grid{
    display: grid;
    grid-template-columns: 51.725% 43.106%;
    justify-content: space-between;
  }
  .dish-card{
    &:not(:last-child){
      margin-bottom: 128px;
    }
  }
  &__left{
    .dish-card{
      &:nth-child(1){
        .dish-card{
          &__img{
            padding-bottom: 149.5%;
          }
        }
      }
    }
  }
  &__right{
    .dish-card{
      &:nth-child(2){
        .dish-card{
          &__img{
            padding-bottom: 148.8%;
          }
        }
      }
    }
  }
}

.pizza{
  background-color: $yellowBg;
  padding: 125px 0 105px;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 20px));
    gap: 73px 40px;
  }
  .dish-card{
    &__img{
      padding-bottom: 147.145%;
    }
    &:nth-child(1){
      grid-column: 1/-1;
      .dish-card{
        &__img{
          padding-bottom: 58.105%;
        }
      }
    }
  }
  .dish-section__nav{
    margin-top: 55px;
  }
}

.sushi{
  padding: 95px 0 100px;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 20px));
    gap: 40px;
  }
  .dish-card {
    &__img {
      padding-bottom: 151%;
    }
    &:nth-child(even){
      margin-top: 100px;
    }
  }
  .dish-section__nav{
    margin-top: 24px;
  }
}

.salad{
  background-color: $yellowBg;
  padding: 65px 0;
  &__grid{
    margin-top: 40px;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 20px));
    gap: 40px;
  }
  &__left{
    .dish-card{
      &:nth-child(1){
        .dish-card{
          &__img{
            padding-bottom: 147.33%;
          }
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 164px 0 50px;
    &__slide{
      grid-template-columns: 100%;
      gap: 36px;
    }
    &__img{
      order: -1;
    }
    &__subtitle{
      font-size: 12px;
      margin-bottom: 16px;
    }
    &__title{
      font-size: 32px;
    }
    &__order{
      margin-top: 26px;
      padding: 14px 0;
    }
    .swiper{
      &-button{
        display: none;
      }
    }
  }

  .dish-section{
    padding: 50px 0;
    &__grid{
      grid-template-columns: 100%;
      gap: 66px;
      margin-top: 66px;
    }
    &__nav{
      justify-content: center;
      margin-top: 40px;
    }
  }

  .new,
  .pizza,
  .sushi,
  .salad{
    &__title{
      font-size: 28px;
    }
  }
}
