.display1{
  font-weight: 700;
  font-size: 48px;
  line-height: 128.7%;
}

.display2{
  font-size: 40px;
  line-height: 125%;
}

.display3{
  font-size: 32px;
  line-height: 128.7%;
}

.display4{
  font-weight: 500;
  font-size: 24px;
  line-height: 125%;
}

.display5{
  font-weight: 500;
  font-size: 20px;
  line-height: 125%;
}

.display6{
  font-size: 18px;
  line-height: 22px;
}

.display7{
  font-weight: 500;
  font-size: 16px;
  line-height: 132.2%;
  letter-spacing: 0.15em;
}

.gray-text{
  color: $gray;
}

.medium-weight{
  font-weight: 500;
}

.body-text{
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 10px;
    }
  }
}

.mlist{
  li{
    line-height: inherit;
    display: flex;
    &::before{
      content: '';
      display: block;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: #000;
      transform: translateY(8px);
      margin-right: 5px;
      flex-shrink: 0;
    }
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
}


@media screen and (max-width: 992px){
  .display1{
    font-size: 32px;
  }

  .display2{
    font-size: 30px;
  }

  .display3{
    font-size: 27px;
  }
}


@media screen and (max-width: 576px){
  .display1,
  .display3{
    font-size: 24px;
  }

  .display2{
    font-size: 20px;
  }

  .display4{
    font-size: 18px;
  }
}
