.catalog{
  padding: 34px 0 100px;
  &__title{
    font-weight: 400;
  }
  &__grid{
    margin-top: 82px;
  }
}

.promo{
  padding: 55px 0 34px;
  &__slider{
    position: relative;
  }
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-button{
      width: unset;
      height: unset;
      &-prev{
        left: -70px;
      }
      &-next{
        right: -70px;
      }
      &-disabled{
        opacity: 0 !important;
      }
    }
  }
}


@media screen and (max-width: 1500px){
  .promo{
    overflow: hidden;
    .swiper{
      &-container{
        overflow: unset;
      }
      &-button{
        display: none;
      }
      &-slide{
        width: 360px;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .catalog{
    &__grid{
      margin-top: 66px;
    }
  }
}


@media screen and (max-width: 576px){
  .promo{
    .swiper{
      &-slide{
        width: 242px;
      }
    }
  }
}
