.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-black-outline{
    border: 1px solid $black;
    letter-spacing: 0.09em;
    font-size: 14px;
    font-weight: 500;
    color: $black;
    &:hover{
        background-color: $black;
        color: #fff;
    }
}

.m-btn-black{
    background-color: $black;
    color: #fff;
    &:hover{
        background-color: rgba($black,.9);
        color: #fff;
    }
}

.btn-line{
    position: relative;
    padding: 13px 0;
    font-weight: 500;
    &:before{
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 4px;
        background-color: $black;
        transition: all .3s;
    }
    &:hover{
        color: $black;
        &:before{
            transform: scaleX(1.2);
        }
    }
}

.gray-btn{
    color: rgba(79, 73, 75, 0.5);
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    &:hover{
        color: rgba(79, 73, 75, 1);
    }
}
