.order{
  padding: 77px 0 33px;
  &__grid{
    display: grid;
    grid-template-columns: auto 460px;
    gap: 20px;
  }
  &__left{
    max-width: 620px;
  }
  &__links{
    margin-top: 42px;
    display: flex;
    align-items: center;
  }
  &__link{
    font-size: 20px;
    line-height: 125%;
    color: rgba(92, 95, 94, 0.5);
    &:hover{
      color: $black;
    }
    &:not(:last-child){
      margin-right: 34px;
    }
    &._active{
      color: $black;
      font-weight: 700;
    }
  }
  &__tabs{
    margin-top: 43px;
    .tab{
      padding-bottom: 12px;
      font-size: 16px;
      line-height: 115.5%;
      &:not(:last-child){
        margin-right: 33px;
      }
    }
  }
  &__form{
    margin-top: 52px;
  }
  &__subtitle{
    margin-bottom: 25px;
    font-weight: 400;
    &:not(:first-child){
      margin-top: 78px;
    }
  }
  &__field{
    display: grid;
    grid-template-columns: auto 121px;
    gap: 39px;
    align-items: flex-end;
    input{
      height: 72px;
    }
    &:not(:last-child){
      margin-bottom: 6px;
    }
    &-btn{
      text-align: left;
      height: 63px;
      display: flex;
      align-items: center;
    }
  }
  &__btn{
    width: 219px;
    height: 45px;
    margin-top: 78px;
    font-weight: 500;
    font-size: 14px;
  }
  &__hint{
    font-size: 14px;
    line-height: 17px;
    margin-top: 24px;
    color: rgba(79, 73, 75, 0.5);
    a{
      text-decoration: underline;
    }
  }
  &__title2{
    font-weight: 400;
    padding: 7px 0;
  }
  &__all{
    margin-top: 50px;
    display: flex;
    justify-content: flex-end;
    font-weight: 400;
    span{
      margin-left: 33px;
    }
  }
  &__group{
    &:not(:last-child){
      margin-bottom: 39px;
    }
    .fg{
      input{
        height: 51px;
      }
      .textarea{
        height: 70px;
      }
    }
    .order__subtitle{
      margin: 0;
    }
    &-hint{
      font-size: 14px;
      line-height: 17px;
      margin-top: 50px;
      color: $gray;
      max-width: 300px;
    }
  }
  &__radios{
    margin-top: 26px;
  }
  &__radio{
    &:not(:last-child){
      margin-bottom: 15px;
    }
  }
  &__desc{
    margin-top: 28px;
    max-width: 520px;
    font-size: 16px;
    line-height: 139.5%;
  }
  &__nav{
    display: flex;
    align-items: center;
    margin-top: 48px;
  }
  &__follow{
    font-size: 16px;
    line-height: 100%;
    padding: 12px 0;
    margin-right: 40px;
    text-transform: uppercase;
  }
  &__new{
    font-size: 16px;
    line-height: 125%;
  }
}


@media screen and (max-width: 1200px){
  .order{
    padding: 60px 0 100px;
    &__grid{
      grid-template-columns: auto 360px;
    }
  }
}


@media screen and (max-width: 992px){
  .order{
    padding: 60px 0 100px;
    &__grid{
      grid-template-columns: 100%;
    }
    &__left{
      max-width: 100%;
    }
    &__right{
      display: none;
    }
    &__tabs{
      margin-top: 36px;
    }
    &__form{
      margin-top: 65px;
    }
    &__btn{
      margin-top: 47px;
    }
    &__hint{
      margin-top: 29px;
    }
  }
}


@media screen and (max-width: 576px){
  .order{
    &--success{
      text-align: center;
    }
    &__field{
      gap: 22px;
      &--wrap{
        grid-template-columns: 100%;
        .fg{
          &__icon{
            transform: unset;
            top: 24px;
          }
        }
        .order__field-btn{
          height: unset;
        }
      }
    }
    &__desc{
      margin-top: 40px;
    }
    &__nav{
      flex-direction: column;
    }
    &__follow{
      margin: 0 0 27px;
      padding: 16px 0;
      font-size: 14px;
      line-height: 100%;
      &:before{
        height: 2px;
      }
    }
  }
}
