.search-card,
.dish-card,
.dish-card2{
  &__img{
    position: relative;
    display: block;
    img{
      position: absolute;
      width: 100%;
      height: 100%;
      object-fit: cover;
      top: 0;
      left: 0;
    }
  }
}


.search-card {
  display: flex;
  flex-direction: column;
  &__img {
    padding-bottom: 65.4%;
  }
  &__title{
    margin-top: 25px;
    display: block;
  }
  &__bottom{
    padding-top: 20px;
    margin-top: auto;
  }
  &__info{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__price,
  &__title{
    font-weight: 500;
    font-size: 16px;
    line-height: 125%;
    text-transform: uppercase;
    color: #5C5F5E;
  }
  &__weight{
    font-size: 14px;
    line-height: 17px;
    color: rgba(92, 95, 94, 0.5)
  }
  &__btn{
    height: 48.24px;
    width: 100%;
    margin-top: 45px;
  }
}

.restaurant-card{
  display: flex;
  flex-direction: column;
  &__img{
    position: relative;
    padding-bottom: 100%;
    background-size: cover;
    background-position: center;
    img{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      max-width: 100%;
      max-height: 100%;
      z-index: 2;
    }
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: $gray;
    }
  }
  &__title{
    margin: 30px 0;
  }
  &__bottom{
    margin-top: auto;
  }
  &__subtitle{
    margin-bottom: 15px;
  }
}

.dish-card{
  &__top{
    position: relative;
  }
  &__img{
    padding-bottom: 70.2%;
  }
  &__cart{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 42px;
    background: rgba($yellowBg, 0.8);
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 0.09em;
    text-transform: uppercase;
    &:hover{
      background: rgba($yellowBg, 1);
      color: $black;
    }
  }
  &__title{
    text-transform: uppercase;
    display: block;
    max-width: 300px;
    margin-top: 32px;
    color: $grayText;
  }
  &__weight{
    font-size: 14px;
    line-height: 17px;
    color: rgba(92, 95, 94, 0.5);
    margin-top: 17px;
  }
  &__price{
    color: $grayText;
    margin-top: 32px;
    font-weight: 400;
  }
}

.dish-card2{
  display: flex;
  flex-direction: column;
  align-items: center;
  &__img{
    padding-bottom: 71%;
    width: 100%;
  }
  &__info{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-top: 52px;
  }
  &__title{
    text-transform: uppercase;
    max-width: 300px;
    color: $grayText;
  }
  &__price{
    color: $grayText;
    font-weight: 400;
  }
  &__weight{
    font-size: 14px;
    line-height: 17px;
    color: rgba(92, 95, 94, 0.5);
    width: 100%;
    margin: 16px 0;
  }
  &__btn{
    margin: auto auto 0 0;
    text-transform: uppercase;
  }
}

.promo-card{
  color: #FFFFFF;
  position: relative;
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(13, 13, 13, 0.4);
  }
  &__content{
    position: relative;
    z-index: 2;
    padding: 37px 40px;
  }
  &__title{
    letter-spacing: 0.05em;
    text-transform: uppercase;
  }
  &__desc{
    font-size: 14px;
    line-height: 126.7%;
    margin: 22px 0 45px;
  }
  &__desc2{
    font-weight: 500;
    font-size: 14px;
    line-height: 105.2%;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.5);
    margin-top: auto;
  }
}

.cart-card{
  display: grid;
  grid-template-columns: 108px auto;
  gap: 50px;
  padding: 33px 0;
  border-bottom: 1px solid rgba(92, 95, 94, 0.5);
  &:last-child{
    border: none;
    padding-bottom: 0;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__info{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  &__title,
  &__price{
    font-size: 16px;
    line-height: 125%;
  }
  &__price{
    padding-bottom: 4px;
  }
  &__info2{
    display: flex;
    align-items: center;
    margin-top: 10px;
    color: rgba(92, 95, 94, 0.5);
    font-size: 14px;
    line-height: 17px;
    &>*{
      line-height: inherit;
    }
  }
  &__weight{
    margin-right: 21px;
  }
  &__calc{
    display: flex;
    align-items: center;
    margin-top: 24px;
    button{
      width: 19px;
      height: 19px;
      border: 1px solid rgba(0, 0, 0, 0.5);
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 10px;
    }
    input{
      width: 40px;
      height: 19px;
      text-align: center;
      font-size: 10px;
      border: none;
      background-color: transparent;
    }
  }
}

.history-card{
  display: flex;
  align-items: center;
  &__img{
    width: 140px;
    flex-shrink: 0;
    margin-right: 18px;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-size: 16px;
    line-height: 125%;
  }
  &__right{
    margin-left: auto;
  }
  &__price{
    font-weight: 400;
  }
  &__rating{
    padding: 8px 0;
    font-size: 12px;
    line-height: 15px;
    letter-spacing: 0.045em;
    text-transform: uppercase;
    margin-top: 16px;
    &:before{
      height: 2px;
    }
  }
}


@media screen and (max-width: 768px){
  .dish-card2{
    &__info{
      margin-top: 24px;
      display: block;
      position: relative;
    }
    &__weight{
      margin: 13px 0 100px;
    }
    &__btn{
      height: 48.24px;
      width: 100%;
      padding: 0;
      border: 1px solid $black;
      text-transform: unset;
      &:before{
        display: none;
      }
      &:hover{
        background-color: $black;
        color: #fff;
      }
    }
    &__price{
      position: absolute;
      top: 100%;
      left: 0;
      margin-top: 54px;
    }
  }
}


@media screen and (max-width: 576px){
  .search-card{
    &__info{
      flex-direction: column-reverse;
      align-items: unset;
    }
    &__price{
      margin-top: 24px;
    }
    &__btn{
      margin-top: 38px;
    }
  }

  .promo-card{
    &__content{
      padding: 25px;
    }
    &__title{
      font-size: 16px;
    }
    &__desc{
      margin: 17px 0 30px;
      font-size: 10px;
    }
    &__desc2{
      font-size: 10px;
    }
  }
}
