.lk{
  padding: 40px 0 76px;
  &__title{
    text-transform: uppercase;
  }
  &__grid{
    display: grid;
    grid-template-columns: 253px calc(100% - 293px);
    gap: 40px;
    margin-top: 51px;
  }
  &__left{
    border-right: $border;
    padding-right: 30px;
  }
}

.lk-sidebar{
  &__link{
    display: flex;
    align-items: center;
    padding: 8px 17px;
    opacity: .5;
    font-size: 14px;
    line-height: 115.5%;
    &:not(:last-child){
      margin-bottom: 17px;
    }
    img{
      margin-right: 23px;
    }
    &._active,
    &:hover{
      color: $black;
      opacity: 1;
      background-color: $yellowBg;
    }
  }
}

.profile{
  padding-left: 66px;
}

.user{
  width: 496px;
  max-width: 100%;
  margin-left: -32px;
  padding: 40px 35px 52px;
  background-color: $yellowBg;
  display: flex;
  &__img{
    width: 107px;
    margin-right: 44px;
  }
  &__name{
    font-weight: 400;
    margin-bottom: 24px;
  }
  &__link{
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 125%;
    &:not(:last-child){
      margin-bottom: 17px;
    }
    img{
      width: 18px;
      margin-right: 11px;
      flex-shrink: 0;
    }
  }
}

.avatar{
  border-radius: 50%;
  overflow: hidden;
  position: relative;
  width: 100%;
  padding-bottom: 100%;
  img{
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

.profile-form{
  margin-top: 26px;
  &__title{
    font-weight: 400;
    margin-bottom: 46px;
  }
  .fg{
    margin-bottom: 28px;
    max-width: 335px;
    input{
      height: 44px;
    }
    label{
      margin-bottom: 8px;
      display: block;
    }
    &__icon{
      width: 18px;
      top: unset;
      bottom: 13px;
      transform: unset;
    }
  }
  &__btn{
    margin-top: 42px;
    padding: 11px 0;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.045em;
    text-transform: uppercase;
    &:before{
      background: #FFDDBF;
    }
  }
  &__checks{
    margin-top: 60px;
  }
  &__check{
    &:not(:last-child){
      margin-bottom: 23px;
    }
  }
}

.history{
  &__nav{
    padding: 0 40px;
    margin-top: 44px;
  }
  &__btn{
    padding: 14px 0;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.045em;
    text-transform: uppercase;
    &:before{
      height: 2px;
    }
  }
}

.history-group{
  padding: 36px 49px 70px;
  &--done{
    border: $border;
  }
  &:not(:last-child){
    margin-bottom: 40px;
  }
  &__items{
    margin-top: 55px;
    display: grid;
    grid-template-columns: 100%;
    gap: 32px;
  }
  &__btn{
    padding: 14px 0;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.045em;
    text-transform: uppercase;
    margin-top: 44px;
    &:before{
      height: 2px;
    }
  }
}

.order-info{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__bottom{
    display: flex;
    align-items: center;
    margin-top: 30px;
  }
  &__status{
    display: flex;
    align-items: center;
    padding: 0 12px;
    height: 30px;
    font-weight: 500;
    font-size: 14px;
    background: #FFDDBF;
    border-radius: 30px;
    flex-shrink: 0;
    margin-right: 31px;
  }
  &__time{
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 125%;
    img{
      margin-right: 10px;
    }
  }
  &__title,
  &__price{
    font-weight: 400;
  }
}

.history-problem{
  margin-top: 88px;
  width: 626px;
  max-width: 100%;
  padding: 45px;
  background-color: $yellowBg;
  &__info{
    display: flex;
    align-items: center;
    img{
      margin-right: 15px;
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    padding: 0 34px;
    margin-top: 30px;
  }
  &__btn{
    padding: 14px 0;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.045em;
    text-transform: uppercase;
    margin-right: 73px;
    &:before{
      height: 2px;
    }
  }
  &__tel{
    font-size: 16px;
    line-height: 125%;
  }
}

.bonus{
  &__title{
    margin-top: 96px;
    color: #5C5F5E;
  }
  &__desc{
    margin-top: 54px;
    display: grid;
    grid-template-columns: 100%;
    gap: 21px;
    font-size: 20px;
    line-height: 132%;
    max-width: 700px;
    p{
      line-height: inherit;
    }
  }
  &__btn{
    margin-top: 55px;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 0.045em;
    text-transform: uppercase;
  }
}

.bonus-card{
  padding: 40px 45px 42px 60px;
  border: $border;
  max-width: 660px;
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__bank,
  &__title{
    font-size: 16px;
    line-height: 125%;
  }
  &__title{
    margin: 13px 0;
  }
  &__ball{
    font-weight: 400;
  }
  &__percents{
    margin-top: 29px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 900;
    font-size: 18px;
    line-height: 23px;
    opacity: 0.9;
  }
  &__percent{
    &--float{
      top: 0;
      position: absolute;
      transform: translateX(-50%);
    }
  }
  &__progress{
    margin-top: 15px;
    position: relative;
    height: 5px;
    background-color: #EBEBF9;
    border-radius: 2px;
    overflow: hidden;
    span{
      height: 100%;
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      background-color: #FFDDBF;
    }
  }
  &__hint{
    margin-top: 26px;
    font-weight: 400;
  }
}

.address{
  &__btn{
    margin-top: 52px;
    padding: 14px 0;
    font-size: 14px;
    line-height: 17px;
    text-transform: uppercase;
    &:before{
      height: 2px;
    }
  }
}

.address-card{
  margin-bottom: 19px;
  padding: 42px 22px 42px 40px;
  background-color: $yellowBg;
  display: flex;
  &__num{
    display: flex;
    align-items: center;
    font-weight: 900;
    font-size: 36px;
    line-height: 45px;
    color: rgba(0, 0, 0, 0.2);
    font-variant-numeric: tabular-nums;
    -webkit-font-feature-settings: "tnum";
    font-feature-settings: "tnum";
    width: 61px;
    flex-shrink: 0;
  }
  &__title,
  &__desc{
    font-size: 16px;
    line-height: 125%;
  }
  &__title{
    color: rgba(92, 95, 94, 0.5);
    font-weight: 500;
  }
  &__desc{
    color: #5C5F5E;
    margin-top: 17px;
  }
  &__nav{
    margin-left: auto;
    display: flex;
    align-items: center;
    height: 12px;
  }
  &__btn{
    &:not(:last-child){
      margin-right: 10px;
    }
  }
}


@media screen and (max-width: 992px){
  .profile{
    padding-left: 0;
  }
  .user{
    margin: 0;
  }
}


@media screen and (max-width: 768px){
  .lk{
    padding: 31px 0 45px;
    &__grid{
      grid-template-columns: 100%;
      gap: 45px;
      margin-top: 37px;
    }
    &__left{
      order: 2;
    }
  }

  .lk-sidebar{
    display: flex;
    justify-content: center;
    &__link{
      height: 82px;
      width: 94px;
      flex-direction: column;
      justify-content: center;
      text-align: center;
      img{
        margin: 0 0 7px;
      }
    }
  }

  .user{
    padding: 65px 12px;
    align-items: unset;
    &__img{
      margin-right: 40px;
      align-self: start;
    }
    &__name{
      font-size: 24px;
      line-height: 30px;
      margin-bottom: 27px;
    }
    &__link{
      &:not(:last-child){
        margin-bottom: 23px;
      }
    }
  }

  .profile-form{
    margin-top: 53px;
    .fg{
      max-width: 100%;
    }
  }

  .bonus{
    &__title{
      margin-top: 89px;
    }
    &__desc{
      margin-top: 46px;
      gap: 28px;
    }
    &__btn{
      margin-top: 50px;
    }
  }

  .bonus-card{
    padding: 0;
    border: none;
    &__top{
      display: block;
    }
    &__qr{
      width: 176px;
      margin: 45px 0 0 -8px;
      img{
        width: 100%;
      }
    }
    &__ball{
      font-size: 24px;
      line-height: 30px;
    }
    &__percents{
      margin-top: 46px;
    }
    &__hint{
      margin-top: 31px;
    }
  }

  .address-card{
    padding-left: 20px;
    margin-bottom: 47px;
    &__num{
      width: 50px;
    }
    &__btn{
      margin-top: 25px;
    }
  }
}
