.logo{
  display: block;
  width: 167px;
  flex-shrink: 0;
  img{
    width: 100%;
    display: block;
  }
}

.header{
  padding: 40px 0;
  position: absolute;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__left,
  &__right{
    display: flex;
    align-items: center;
  }
  &__restaurant{
    padding: 4px 0;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    margin-left: 66px;
    &-btn{
      position: relative;
      font-weight: inherit;
      color: inherit;
      &:before{
        content: '';
        position: absolute;
        height: 2px;
        width: 100%;
        top: 100%;
        background-color: rgba($black,.5);
        margin-top: 4px;
        transition: all .3s;
      }
      &:hover{
        &:before{
          transform: scaleX(1.2);
        }
      }
      &-text2{
        display: none;
      }
    }
    &-title2{
      display: none;
    }
  }
  &__cart{
    display: flex;
    align-items: center;
    margin-right: 58px;
    &-price{
      margin-left: 14px;
      padding-left: 14px;
      height: 20px;
      display: flex;
      align-items: center;
      border-left: 1px solid rgba(0, 0, 0, 0.5);
      font-weight: 700;
      font-size: 14px;
      line-height: 18px;
      white-space: nowrap;
    }
  }
  &__buttons{
    display: flex;
    align-items: center;
    margin-right: 46px;
  }
  &__btn{
    &:not(:last-child){
      margin-right: 27px;
    }
    &:hover{
      opacity: .7;
      color: $black;
    }
  }
  &__phone,
  &__address{
    display: flex;
    align-items: center;
    font-size: 14px;
    line-height: 17px;
    img{
      margin-right: 10px;
    }
  }
  &__phone{
    margin-right: 68px;
    &:hover{
      opacity: .7;
      color: $black;
    }
  }
  &__search2{
    width: 18px;
    display: none;
  }
  &._open{
    .menu{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &._search{
    .header-search{
      opacity: 1;
      pointer-events: unset;
    }
    .header__search1{
      display: none;
    }
    .header__search2{
      display: unset;
    }
  }
}

.menu{
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  z-index: 100;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  &__bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $gray;
  }
  &__wrap{
    position: relative;
    z-index: 2;
    width: 477px;
    max-width: calc(100% - 50px);
    background-color: #fff;
    margin-left: auto;
    height: 100%;
    overflow-y: auto;
    padding: 0 40px 50px;
  }
  &__top{
    padding: 50px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #fff;
    position: sticky;
    top: 0;
  }
  &__logo{
    width: 149px;
  }
  &__links,
  &__links2{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__links{
    //margin-top: 52px;
  }
  &__links2{
    margin-top: 45px;
  }
  &__link{
    margin-right: auto;
    font-size: 20px;
    line-height: 115.5%;
    &:not(:last-child){
      margin-bottom: 14px;
    }
    &-hint{
      font-weight: 400;
      font-size: 10px;
      line-height: 180%;
      color: #E37552;
      margin-left: 5px;
    }
  }
  .header__phone{
    margin: 37px 0 0 0;
    display: flex;
  }
  .header__address{
    margin-top: 23px;
  }
}

.header-search{
  background-color: #fff;
  position: fixed;
  top: $headerHeight;
  left: 0;
  z-index: 10;
  width: 100%;
  height: calc(100vh - 115px);
  padding: 52px 0;
  overflow-y: auto;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  &__title{
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    text-transform: uppercase;
    margin-top: 85px;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 30px));
    gap: 40px;
    margin-top: 33px;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 55px;
  }
  &__all{
    text-transform: uppercase;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 0.045em;
  }
}

.burger{
  width: 32px;
  height: 16px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  span{
    width: 100%;
    height: 2px;
    border-radius: 6px;
    background-color: $black;
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 40px 0 0;
    &__inner{
      padding: 23px 0 23px 43px;
    }
    &__restaurant{
      position: absolute;
      top: 0;
      left: 0;
      padding: 0 16px;
      height: 40px;
      display: flex;
      align-items: center;
      background-color: $yellowBg;
      width: 100%;
      margin: 0;
      &-btn{
        margin-left: auto;
        &:before{
          margin-top: 1px;
          height: 1px;
        }
        &:after{
          content: '';
        }
        &-text1{
          display: none;
        }
        &-text2{
          display: unset;
        }
      }
      &-title2{
        display: unset;
        margin-left: 4px;
      }
    }
    &__phone{
      display: none;
    }
    &__cart{
      margin-right: 0;
    }
  }

  .burger{
    position: absolute;
    left: 16px;
    bottom: 29px;
    width: 25px;
    height: 8px;
    span{
      height: 1px;
    }
  }

  .header-search{
    height: calc(100vh - 121px);
    top: 121px;
    padding: 40px 0;
    &__field{
      input{
        height: 35px;
        padding-left: 25px !important;
        font-size: 12px;
      }
      .fg__icon{
        width: 15px;
        left: 0;
      }
    }
    &__grid{
      grid-template-columns: repeat(3,calc(33.33% - 20px));
      gap: 80px 30px;
    }
  }
}


@media screen and (max-width: 768px){
  .header-search{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 15px));
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    padding-top: 30px;
    &__logo{
      width: 96px;
    }
    &__cart{
      &-price{
        padding-left: 9px;
        margin-left: 9px;
        font-size: 10px;
        line-height: 13px;
      }
      &-icon{
        img{
          width: 12px;
        }
      }
    }
    &__buttons{
      margin-right: 18px;
    }
    &__btn{
      width: 11px;
      &:not(:last-child){
        margin-right: 14px;
      }
      img{
        width: 100%;
      }
    }
    &__restaurant{
      font-size: 10px;
      line-height: 12px;
      height: 30px;
    }
  }

  .burger{
    bottom: 26px;
  }

  .header-search{
    height: calc(100vh - 97px);
    top: 97px;
    &__all{
      font-size: 14px;
      line-height: 17px;
      &:before{
        height: 2px;
      }
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
