.footer{
  padding: 99px 0 68px;
  &__logo{
    width: 157px;
  }
  &__top{
    display: flex;
    justify-content: space-between;
    margin-top: 48px;
  }
  &__title,
  &__link,
  &__link2{
    font-size: 14px;
    line-height: 139.5%;
  }
  &__title{
    font-weight: 700;
    margin-bottom: 26px;
  }
  &__store{
    width: 110px;
    height: 37px;
    border-radius: 5px;
    padding: 0 9px;
    img{
      max-width: 100%;
      max-height: 100%;
    }
    &:not(:last-child){
      margin-bottom: 12px;
    }
  }
  &__links{
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  &__link{
    margin-right: auto;
    &:not(:last-child){
      margin-bottom: 13px;
    }
  }
  &__socials{
    display: flex;
    align-items: center;
    margin-top: 32px;
  }
  &__social{
    &:not(:last-child){
      margin-right: 26px;
    }
    img{
      transition: all .3s;
    }
    &:hover{
      img{
        transform: translateY(-10px);
      }
    }
  }
  &__form{
    width: 204px;
    display: grid;
    grid-template-columns: auto 41px;
    gap: 5px;
    input{
      height: 41px;
      font-size: 14px;
      font-weight: 700;
      &::placeholder{
        color: rgba(0, 0, 0, 0.4);
      }
    }
  }
  &__btn{
    height: 41px;
    width: 41px;
    svg{
      height: 15px;
      width: auto;
    }
    &:hover{
      path{
        fill: #fff;
      }
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-top: 43px;
    padding-top: 25px;
    border-top: $border;
  }
  &__link2{
    color: rgba(0, 0, 0, 0.5);
  }
  &__c{
    margin-right: auto;
  }
  &__conf{
    margin-left: 63px;
  }
}


@media screen and (max-width: 768px){
  .footer{
    padding: 30px 0;
    &__top{
      margin-top: 59px;
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 63px 20px;
    }
    &__bottom{
      margin-top: 85px;
      flex-direction: column;
    }
    &__link2{
      margin: 0 auto 22px 0;
    }
    &__c{
      order: 2;
      margin-top: 18px;
      margin-bottom: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__col{
      &:nth-child(3),
      &:nth-child(4){
        grid-column: 1/-1;
      }
    }
  }
}
