.search{
  padding: 88px 0 30px;
  &__title{
    font-weight: 400;
    span{
      font-size: 24px;
      line-height: 128.7%;
      color: $gray;
      transform: translateY(-3px);
      display: inline-block;
    }
  }
  &__grid{
    margin-top: 89px;
  }
  &__nav{
    margin-top: 119px;
    display: flex;
    justify-content: center;
  }
}


@media screen and (max-width: 992px){
  .search{
    padding-top: 40px;
    &__title{
      span{
        font-size: 18px;
      }
    }
    &__grid{
      margin-top: 55px;
    }
  }
}


@media screen and (max-width: 576px) {
  .search {
    &__title {
      br{
        display: none;
      }
      span {
        font-size: 12px;
      }
    }
    &__nav{
      margin-top: 70px;
    }
    &__link{
      font-size: 14px;
      line-height: 17px;
      &:before{
        height: 2px;
      }
    }
  }
}
