.catalog-item{
  padding: 50px 0 30px;
  &__inner{
    background-color: $yellowBg;
    padding: 56px 60px 47px;
    display: grid;
    grid-template-columns: 57.6% auto;
    gap: 60px 30px;
  }
  &__right{
    max-width: 323px;
    margin-left: auto;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__hint{
    margin-top: 25px;
    font-size: 16px;
    line-height: 125%;
    max-width: 500px;
  }
  &__title{
    text-transform: uppercase;
  }
  &__types{
    display: flex;
    margin-top: 34px;
  }
  &__type{
    display: flex;
    flex-direction: column;
    align-items: center;
    &:not(:last-child){
      margin-right: 20px;
    }
    &-title{
      font-size: 16px;
      line-height: 125%;
      color: $gray;
      margin-top: 4px;
    }
  }
  &__desc{
    display: flex;
    align-items: center;
    margin-top: 45px;
    font-size: 16px;
    line-height: 139.5%;
    color: $gray;
    text-transform: uppercase;
    img{
      margin-left: 14px;
    }
    &:hover{
      color: $black;
    }
  }
  &__text{
    margin-top: 24px;
    font-size: 16px;
    line-height: 139.5%;
    max-width: 300px;
  }
  &__price{
    font-weight: 500;
    font-size: 16px;
    line-height: 125%;
    margin-top: 32px;
  }
  &__nav{
    margin-top: 29px;
  }
  &__btn{
    text-transform: uppercase;
    letter-spacing: 0.09em;
  }
  &__toppings{
    margin-top: 37px;
    &-title{
      font-weight: 500;
      font-size: 16px;
      line-height: 139.5%;
      color: $gray;
      text-transform: uppercase;
    }
    &-wrap{
      display: flex;
      flex-wrap: wrap;
      margin-top: 12px;
      width: calc(100% + 21px);
    }
  }
}

.topping{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 65px;
  height: 65px;
  margin: 12px 21px 0 0;
  border: $border;
  transition: all .3s;
  &__title,
  &__price{
    font-size: 12px;
    line-height: 115.5%;
  }
  &__title{
    margin-bottom: 5px;
  }
  &__price{
    color: $gray;
  }
  &__calc{
    display: none;
    align-items: center;
    input{
      width: 18px;
      background-color: transparent;
      border: none;
      text-align: center;
      font-size: 12px;
      line-height: 115.5%;
    }
    button{
      font-size: 12px;
      line-height: 115.5%;
    }
  }
  &._active{
    .topping__price{
      display: none;
    }
    .topping__calc{
      display: flex;
    }
  }
  &:hover{
    border-color: $black;
  }
}

.info-modal{
  .modal{
    &-dialog{
      background-color: #fff;
      padding: 55px 58px 76px;
      width: 685px;
      display: inline-flex;
      flex-direction: column;
      align-items: center;
    }
  }
  &__title,
  &__desc{
    font-size: 16px;
    line-height: 139.5%;
    width: 100%;
  }
  &__desc{
    margin-top: 39px;
    p{
      line-height: inherit;
    }
  }
  &__props{
    display: flex;
    margin: 60px auto 0 0;
    border-top: $border;
    padding-top: 26px;
  }
  &__prop{
    &:not(:last-child){
      margin-right: 45px;
    }
    &-title{
      font-size: 14px;
      line-height: 139.5%;
    }
    &-desc{
      font-size: 13px;
      line-height: 139.5%;
      color: #BDBDBD;
      margin-top: 8px;
    }
  }
}

.composition{
  display: none;
}

.advantages{
  padding: 30px 0 38px;
  &__inner{
    padding: 64px 60px;
    border: $border;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__item{
    display: flex;
    align-items: center;
  }
  &__icon{
    flex-shrink: 0;
    width: 40px;
    height: 40px;
    margin-right: 29px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color: $yellowBg;
  }
  &__title{
    font-size: 18px;
    line-height: 22px;
  }
}

.add-to-order{
  padding: 38px 0 120px;
  &__title{
    margin-bottom: 54px;
    text-transform: uppercase;
    font-weight: 400;
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    margin-top: -81px;
    background-color: $yellowBg;
    padding: 89px 0 71px;
    &__inner{
      padding: 177px 0 0;
      grid-template-columns: 100%;
      position: relative;
    }
    &__hint{
      display: none;
    }
    &__right{
      margin: 0;
      max-width: 100%;
      display: flex;
      flex-direction: column;
    }
    &__top{
      position: absolute;
      left: 0;
      top: 0;
    }
    &__desc{
      margin: 0;
      pointer-events: none;
      img{
        display: none;
      }
    }
    &__text{
      margin-top: 20px;
      max-width: 100%;
    }
    &__price{
      margin-top: 51px;
      order: 2;
    }
    &__toppings{
      &-wrap{
        margin-top: 5px;
      }
    }
    &__nav{
      order: 3;
      margin-top: 18px;
    }
    &__btn{
      padding: 12px 0;
      font-size: 14px;
      &:before{
        height: 2px;
      }
    }
    .info-modal{
      display: block !important;
      position: static !important;
      opacity: 1 !important;
      padding: 15px 0;
      border-top: 1px solid rgba(0, 0, 0, 0.5);
      border-bottom: 1px solid rgba(0, 0, 0, 0.5);
      margin-top: 30px;
      &:before{
        display: none;
      }
      .modal{
        &-dialog{
          margin: 0;
          width: 100%;
          max-width: 100%;
          transform: unset !important;
          padding: 0;
          background-color: transparent;
        }
        &__close{
          position: static;
          pointer-events: none;
          img{
            transition: all .3s;
            width: 15px;
            opacity: .5;
            transform: rotate(-45deg) translateY(-3px);
          }
        }
      }
      &__top{
        cursor: pointer;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        &._active{
          .modal__close{
            img{
              transform: rotate(0);
            }
          }
        }
      }
      &__title{
        font-size: 12px;
        letter-spacing: 0.09em;
        text-transform: uppercase;
        font-weight: 500;
        color: $gray;
      }
      &__props{
        border: none;
        padding: 0;
        margin-top: 30px;
      }
      &__bottom{
        padding-bottom: 20px;
        display: none;
      }
    }
  }
}


@media screen and (max-width: 768px){
  .advantages{
    padding: 51px 0;
    &__inner{
      display: grid;
      grid-template-columns: 100%;
      gap: 63px;
    }
  }

  .add-to-order{
    padding: 40px 0;
    &__title{
      margin-bottom: 40px;
    }
  }
}
