.fg {
  position: relative;
  margin: 0;
  input,
  select,
  textarea{
    border: none;
    border-bottom: 1px solid $black;
    height: 52px;
    width: 100%;
    font-size: 18px;
    line-height: 22px;
    background-color: transparent;
    color: $black;
    &::placeholder {
      color: $black;
    }
  }
  &__icon{
    position: absolute;
    left: 8px;
    top: 50%;
    transform: translateY(-50%);
    &+input{
      padding-left: 44px;
    }
  }
  textarea{
    resize: none;
  }
  select{
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    background-image: url(../img/select-arrow.svg);
    background-repeat: no-repeat;
    background-position: right 5px center;
    &::-ms-expand {
      display: none; // IE
    }
    option{
      padding-left: 10px;
    }
  }
  label{
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    display: block;
    color: $gray;
  }
  &__hint{
    position: absolute;
    background: #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
    top: 100%;
    left: 0;
    width: 100%;
    padding: 14px;
    z-index: 5;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
    &-title,
    &-desc{
      font-size: 14px;
      line-height: 17px;
    }
    &-title{
      margin-bottom: 9px;
      color: $gray;
    }
  }
  ._show-hint{
    &~.fg__hint{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.m-check,
.m-radio{
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        background-position: center;
        background-repeat: no-repeat;
      }
    }
  }
  label {
    position: relative;
    min-height: 20px;
    font-size: 16px;
    line-height: 125%;
    cursor: pointer;
    display: flex;
    align-items: center;
    &:before {
      content: '';
      border: 1px solid #C1D8D9;
      width: 16px;
      height: 16px;
      left: 0;
      top: 50%;
      margin-right: 10px;
      flex-shrink: 0;
    }
  }
}


.m-radio{
  label{
    &:before{
      border-radius: 50%;
    }
  }
  input{
    &:checked+label {
      &::before {
        background-image: url(../img/radio.svg);
      }
    }
  }
}

.m-check{
  input{
    &:checked+label {
      &::before {
        background-image: url(../img/check2.svg);
      }
    }
  }
}

.photo-input{
  position: relative;
  padding-bottom: 100%;
  input{
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
    opacity: 0;
  }
  label{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    left: 0;
    border: 1px solid rgba(0, 0, 0, 0.5);
    border-radius: 50%;
  }
}

.rating {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  &--read-only{
    pointer-events: none;
  }
  &__item {
    &:not(:first-child) {
      margin-right: 5px;
    }
    &:hover {
      cursor: pointer;
      path{
        fill: #FFC795;
        fill-opacity: 1;
      }
      &~.rating__item {
        path{
          fill: #FFC795;
          fill-opacity: 1;
        }
      }
    }
  }
}

.rating[data-total-rating="1"] .rating__item:nth-child(n+5),
.rating[data-total-rating="2"] .rating__item:nth-child(n+4),
.rating[data-total-rating="3"] .rating__item:nth-child(n+3),
.rating[data-total-rating="4"] .rating__item:nth-child(n+2),
.rating[data-total-rating="5"] .rating__item:nth-child(n+1) {
  path{
    fill: #FFC795;
    fill-opacity: 1;
  }
}


@media screen and (max-width: 576px){
  .fg{
    &__icon{
      left: 0;
      &~input{
        padding-left: 39px;
      }
    }
  }
}
